<template>
    <div class="role-prmsns-box" v-if="isShow">
        <div class="prmsns-title-line">
            <div class="eidtuser-title-line">
                <div class="title-text">{{ $t('dlgpermisson.accessControl') }}</div>
                <span class="edituser-box-close" @click="cancel">
                    <svgloader class="icon-close" icon="Close" size="1.2rem"></svgloader>
                </span>
            </div>
            <div class="prmsn-role-person">
                <svgloader class="prmsn-role-icon" :icon="roleIcon" size="2.5rem"></svgloader>
                <div class="prmsn-role-name">{{ $t('dlgpermisson.role') }}：{{ group_name }}</div>
            </div>

            <div class="prmsns-cnfrm-btn" @click="updateRolePrmsns">{{ $t('dlgpermisson.sure') }}</div>
        </div>
        <div class="role-prmsns-list" v-show="group_id != -1">
            <div class="role-prmsn-item" v-for="(item, i) in trgtRolePrmsns.modules" :key="i">
                <div class="role-prmsn-name div-width">{{ item.module_desc }}</div>
                <div class="role-prmsn-show" :class="{ 'blckColor': item.level == 1 }" @click="item.level = 1">
                    <!-- <svgloader class="test"  icon="Eye" size="1.25rem"></svgloader> -->
                    {{ $t('dlgpermisson.visable') }}
                </div>
                <div class="div-checkbox">
                    <CheckCircle class="checkbox" v-model="item.level" :activeValue="1" :staticValue="0">
                    </CheckCircle>
                </div>
                <div class="role-prmsn-edit" :class="{ 'blckColor': item.level == 4 }" @click="item.level = 4">
                    <!-- <svgloader  class="test"  icon="Pen" size="1.25rem"></svgloader> -->
                    {{ $t('dlgpermisson.operational') }}
                </div>
                <div class="div-checkbox">
                    <CheckCircle class="checkbox" v-model="item.level" :activeValue="4" :staticValue="0">
                    </CheckCircle>
                </div>
            </div>
            <!-- <div class="role-prmsn-item" style="border:none;"></div> -->
        </div>
    </div>
</template>
<script>
import {
    authGroupModulesLstApi,
    authModulesRightModApi
} from '../../../api/authorize/authorize'
import { deepCopy } from "../../../utils/deepCopy.js"
import svgloader from '../../../components/icon_font/svgloader.vue'
import CheckCircle from '../../../components/controls/CheckCircle.vue'
import { messageApi } from '../../../components/message/message'
import { maskApi } from '../../mask/StackMask.vue'
import { vuexStore } from '@/store/storeHelper'
export default {
    components: {
        svgloader,
        CheckCircle
    },
    data() {
        return {
            trgtRolePrmsns: {
                "edituser": 0,
                "debug": 0,
                "manageSys": 0,
                "manageUsers": 0,
            },
            mdls: {},
            isShow: true,
            roleIcon: "Worker",
            maskId: 0,
        }
    },
    methods: {
        updateRolePrmsns() {
            this.isShow = false
            this.modPrmsns()
            this.onConfirm && this.onConfirm()
            maskApi.descend(this.maskId)
        },
        cancel() {
            this.isShow = false
            this.onCancel && this.onCancel();
            maskApi.descend(this.maskId);
        },
        getPrmsns(id) {
            authGroupModulesLstApi(id).then((result) => {
                if (result.status == 0) {
                    this.trgtRolePrmsns = result.data
                    console.log("1111111", result.data);
                } else {
                    messageApi.error(['获取用户组权限失败'])
                }
            })
        },
        async modPrmsns() {
            let nmds = {}
            nmds['group_id'] = this.group_id;
            nmds['modules'] = deepCopy(this.trgtRolePrmsns['modules']);
            nmds["reload"] = vuexStore.state.f_status.group_id == this.group_id;
            await authModulesRightModApi(nmds).then((result) => {
                //TODO 如果要保存登录状态,此处必须添加处理
                if (vuexStore.state.f_status.group_id == this.group_id) {
                    if (result && result.data)
                        vuexStore.state.f_status.permissions = result.data;
                    else {
                        vuexStore.state.f_status.permissions = {};
                    }
                }
                messageApi.success(['更新成功'])
            }).catch(() => {
                messageApi.success(['更新失败'])
            });
        },
        updateRole() {
            switch (this.group_id) {
                case 1:
                    this.roleIcon = 'Super'
                    break
                case 2:
                    this.roleIcon = 'Admin'
                    break
                case 4:
                    this.roleIcon = 'Engineer'
                    break
                default:
                    this.roleIcon = 'Worker'

            }
        },
        initStatus() {
            this.maskId = maskApi.rise(15, this.cancel)
        }
    },
    props: {
        group_name: {
            type: String,
            default: () => { return this.$t('dlgpermisson.worker') }
        },
        group_id: {
            type: Number,
            default: 1
        }

    },
    created() {
        this.getPrmsns(this.group_id)
        this.updateRole()
    },
    watch: {
        group_id: {
            immediate: true,
            handler: function () {
                this.updateRole()
            }
        }
    },
    mounted() {
        this.initStatus()
    },
}
</script>


