<template>
    <div class="addrole-box" v-if="isShow">
        <div class="addrole-title-line">
            <span class="addrole-title-msg">{{$t('newrole.newgroup')}}</span>
            <span class="addrole-box-close"  @click="cancel">
                <svgloader class="icon-close" icon="Close" size="1.2rem"></svgloader>
            </span>
        </div>
        <div class="addrole-new-line">
            <div class="addrole-name-text">
                {{$t('newrole.groupname')}}
            </div>
            <InputText fntFmly="--R-M" :imprprChrctr="['space']"  class="addrole-name-input" type="text" v-model="roleData.group_name" :textValid="roleNameValid" :validReason="roleNameReason"></InputText>
        </div>
        <div class="addrole-operate-line">
            <div class="addrole-cancel-btn" @click="cancel">{{$t('newrole.cancel')}}</div>
            <div class="addrole-cnfrm-btn"  @click="addRole" v-show="validType==-1">{{$t('newrole.confirm')}}</div>
        </div>
    </div>
</template>
<script>
import svgloader from '../../../components/icon_font/svgloader.vue'
import InputText from '../../../components/input/InputText.vue'
import { maskApi } from '../../mask/StackMask.vue'

export default {
    components:{
        InputText,
        svgloader
    },
    data() {
        return {
            roleData:{
                group_name:{
                    text:"",
                    valid:false,
                },
                group_desc:{
                    text:"",
                    valid:false,
                },
            },
            isShow:true,
            groupNames:[],
            validType:-1,
            maskId:0,
        }
    },
    methods: {
        addRole(){
            this.isShow=false
            this.onConfirm && this.onConfirm(this.roleData)
            maskApi.descend(this.maskId)
        },
        cancel(){
            this.isShow=false
            this.onCancel && this.onCancel()
            maskApi.descend(this.maskId)
        },
        initStatus(){
            this.maskId=maskApi.rise(15,this.cancel)
        },
        initRoleNames(){
            for(let item of this.groups){
                this.groupNames.push(item.group_name)
            }
        }
    },
    computed:{
        roleNameValid(){
            return this.validType==-1
        },
        roleNameReason(){
            if(this.validType==1){
                return this.$t('dlgaddrole.reasonOne')
            }
            if(this.validType==2){
                return this.$t('dlgaddrole.reasonTwo')
            }
            return ""
        }
    },
    props:{
        prmsnRoles:{
            type:Object,
            default:()=>{
                return {
                    [()=>{return this.$t('dlgaddrole.worker')}]:0,
                    [()=>{return this.$t('dlgaddrole.technician')}]:1,
                    [()=>{return this.$t('dlgaddrole.engineer')}]:2,
                    [()=>{return this.$t('dlgaddrole.manager')}]:3,
                    [()=>{return this.$t('dlgaddrole.supervisor')}]:4,
                    [()=>{return this.$t('dlgaddrole.boss')}]:5
                }
            }
        },
        groups:{
            type:Object,
            default:function(){
                return {}
            }
        }
    },
    watch:{
        roleData:{
            deep:true,
            immediate:true,
            handler:function(){
                let name=this.roleData.group_name.text
                if(name==""){
                    this.validType=1
                }
                else if(this.groupNames.indexOf(name)!=-1){
                    this.validType=2
                }else{
                    this.validType=-1
                }
            }
        }
    },
    mounted() {
        this.initStatus()
        this.initRoleNames()
    },
}
</script>
<style scoped>
.addrole-box{
    width: 37.5rem;
    height: 31.25rem;
    background-color: var(--fff);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    border-radius: 0.375rem;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        z-index: 40;
}
.addrole-title-line{
    position: absolute;
    top: 0;
    width: 100%;
    height: 3.2688rem;
    background-color: var(--Cee);
    color: var(--C3B);
    border-radius: 0.375rem;;
}
.addrole-title-msg{
    position: absolute;
    display: inline-block;
    left: 50%;
    transform: translate(-50%,-50%);
    top: 50%;
    height: 2.4375rem;
    font-size:var(--font1_75);
    line-height: 2.4375rem;
    letter-spacing: 0px;
    color: var(--C3B);
}
.addrole-box-close{
    display: inline-block;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 3rem;
    height: 3rem;
    color: var(--C65);
}
.icon-close{
    position: absolute;
    right: 1.0625rem;
    top: 50%;
    transform: translateY(-50%);
}
.addrole-new-line{
    position: absolute;
    width: 100%;
    top: 45%;
    transform: translateY(-50%);
    height: 3.125rem;
}
.addrole-role-line{
    position: absolute;
    width: 100%;
    top: 14.125rem;
    height: 3.125rem;
}
.addrole-name-text,.addrole-role-text{
    position: absolute;
    left: 3.23rem;
    top: 50%;
    transform: translateY(-50%);
    height:1.5625rem;
    font-size:var(--font1_625);
    line-height:1.5625rem;
    color: var(--C3B);
}
.addrole-name-input{
    position: absolute;
    height: 100%;
    width: 18.4768rem;
    text-align: center;
    font-size: 1.125rem;
    right: 3.2106rem;
    border-radius: 0.25rem;
    color: var(--C3B);
    background-color: var(--fff);
    border: 1px solid  var(--Cf1);
    box-sizing: border-box;
}
.addrole-operate-line{
    position: absolute;
    width: 100%;
    bottom: 3.15rem;
    height: 3.125rem;
}
.addrole-cnfrm-btn{
    text-align: center;
    position: absolute;
    top:0;
    height:  3.125rem;
    line-height:  3.125rem;
    width: 8.625rem;
    right: 8.4375rem;
    background-color: var(--C00c155);
    font-size:var(--font1_625);
    color: var(--fff);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius:0.25rem ;
}
.addrole-cancel-btn{
    text-align: center;
    position: absolute;
    top:0;
    height:3.125rem;
    line-height:3.125rem;
    width: 8.625rem;
    left: 8.4375rem;
    background-color: var(--Cf1);
    font-size:var(--font1_625);
    color: var(--C00);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius:0.25rem ;
}
</style>