<template>
    <div class="users-mng-box" @click="isClickOpearteBox">
        <div class="users-nav">
            <span class="id">ID</span>
            <span class="rolename" ref="editUsr">{{ $t('setrole.rolename') }}</span>
        </div>
        <div class="mask-box">
            <div class="table-box">
                <div v-for="(item, i) in groups" :key="i" :class="{ 'users-item': true, 'active-user': actvRole == i }"
                    @click="operateUsr(i)">
                    <!-- <span>{{item.group_id}}</span> -->
                    <span class="id">{{ i + 1 }}</span>
                    <span class="rolename">{{ item.group_name }}</span>
                    <span class="edit" v-if="permision_level == 4">
                        <svg-loader @click="editRolePrmsns" v-show="actvRole == i" class="edit-btn"
                            icon="ActiveEditUser" size="2.5rem"></svg-loader>
                        <svg-loader @click="delGroup" v-show="actvRole == i" class="trash-btn" icon="DelDmrct"
                            size="2.5rem"></svg-loader>
                    </span>
                </div>
                <div :class="{ 'users-item': true, 'add-line': true }">
                    <span class="id" v-if="permision_level == 4">
                        <LeetxButton @click="addRoleDialog" class="add-btn" :use-icon="true" icon="StaticCircleAdd"
                            bgcColor="transparent" height="2.5rem" width="2.5rem" iconSize="2.5rem">
                        </LeetxButton>
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { authGroupAddApi, authGroupDelApi, authGroupLstApi } from "../../api/authorize/authorize.js"
import rolePrmsnsEdit from "../../dialogs/permission/roleprmsn/editRolePrmsns.js"
import roleAdd from '../../dialogs/permission/addrole/addrole.js'
import cnfrmWin from '../../dialogs/common/confirm/cnfrm.js'
import LeetxButton from "../controls/LeetxButton.vue"
import { messageApi } from '../message/message.js'

export default {
    components: { LeetxButton },
    data() {
        return {
            module: "AUTH_GROUP",
            actvRole: -1,
            itemsLength: 10,
            groups: [],
            newRole: {
                group_name: {
                    text: "",
                    valid: false,
                },
                group_desc: {
                    text: "",
                    valid: false,
                },
            },

        }
    },
    methods: {
        operateUsr(i) {
            this.actvRole = this.actvRole == i ? -1 : i

        },
        editRolePrmsns() {
            rolePrmsnsEdit(
                {
                    "group_id": this.groups[this.actvRole].group_id,
                    "group_name": this.groups[this.actvRole].group_name
                }
            )
        },
        addRoleDialog() {
            let groups = this.groups
            roleAdd({ groups }).then(async (result) => {
                for (let key in this.newRole) {
                    this.newRole[key] = result[key].text
                }
                this.addGroup(this.newRole)
            })
        },
        async addGroup(group) {
            await authGroupAddApi(group).then((result) => {
                if (result.status != 0) {
                    messageApi.error(['添加用户组失败'])
                }
            }).catch(() => {
                messageApi.error(['添加用户组失败'])
            });
            this.getGroupList()
        },
         delGroup() {
            // let isDel = false
            let group_id = this.groups[this.actvRole].group_id
            let title = this.$t('setrole.delgroup')
            let confirmText = this.$t('setrole.yes')
            let cancelText = this.$t('setrole.no')
            let msg = this.$t('setrole.isDelGroup')
            cnfrmWin({ title, confirmText, cancelText, msg }).then(() => {
                authGroupDelApi(group_id)
                if (this.groups.length == 1) {
                    this.groups = []
                }
                this.getGroupList()
                this.resetRole()
            })


        },
        getGroupList() {
            authGroupLstApi().then((result) => {
                if (result.status == 0) {
                    this.groups = result.data
                } else {
                    this.groups = []
                }

            }).catch(() => {
                this.groups = []
            });
        },
        resetRole() {
            this.actvRole = -1
        },
        resetData() {
            this.actvRole = -1,
                this.itemsLength = 10,
                this.groups = [],
                this.newRole = {
                    group_name: {
                        text: "",
                        valid: false,
                    },
                    group_desc: {
                        text: "",
                        valid: false,
                    }
                }
        }
    },
    created() {
        if (this.permision_level >= 1)
            this.getGroupList()
    },
    // watch: {
    //     isOnline: {
    //         immediate: true,
    //         deep: true,
    //         handler() {
    //             if (this.isLogin != this.isOnline) {
    //                 this.isLogin = this.isOnline
    //                 if (this.isOnline) {
    //                     this.getGroupList()
    //                 } else {
    //                     this.resetData()
    //                 }
    //             }

    //         }
    //     }
    // },
    computed: {
        // 用户是否在线
        permision_level() {
            if (this.module in this.$store.state.f_status.permissions)
                return this.$store.state.f_status.permissions[this.module]
            else
                return 0
        },
    },
}
</script>
<style scoped>
li {
    list-style: none;
}

.users-mng-box {
    position: relative;
    top: 1rem;
    left: 50%;
    transform: translateX(-50%);
    width: 84.5vw;
    height: 100%;
    font-size: var(--font1_5);
    border-top: 2px solid var(--C00);
}

.users-nav {

    display: flex;
    width: 84.5vw;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    height: 3.9063vh;
    line-height: 3.9063vh;
    color: var(--C65);
    border-bottom: 1px solid var(--C00);

}

.id {
    /* flex: 1; */
    width: 25%;
}

.rolename {
    /* flex: 1; */
    width: 40%;
}

.edit {
    /* flex: 1; */
    width: 35%;
}

.mask-box {
    position: relative;
    width: 84.5vw;
    left: 50%;
    transform: translateX(-50%);
    height: 60vh;
    overflow: hidden;
}

.table-box {
    position: relative;
    width: calc(84.5vw + 18px);
    overflow-y: scroll;
    overflow-x: hidden;
    height: 100%;
}

.users-item {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    height: 3.9063vh;
    line-height: 3.9063vh;
    display: flex;
    width: 84.5vw;
    border-bottom: 1px solid var(--CD4);
    color: var(--C3B);
    font-size: var(--font1_625);
}

.users-item span {
    position: relative;
    /* flex: 1; */
}

.users-item span:last-child {
    cursor: pointer;
}

.active-user {
    color: var(--C00c155) !important;
}

.usr-operate {
    position: absolute;
    left: 0;
    top: 0;
    width: 11.625rem;
    height: 5.5rem;
    background: rgba(112, 112, 112, 0.9);
    border-radius: 0.375rem;
    display: flex;
    z-index: 19;

}

.usr-operate div {
    flex: 1;
    padding: 10px 0;
    color: var(--fff);
}

.active-box:hover,
.edit-box:hover {
    cursor: pointer;
}

.active-box:active,
.edit-box:active {
    color: var(--C00c155) !important;
}

.delete-box:active {
    color: var(--CE61F00);
}

.usr-operate div span {
    color: var(--fff);
}

.trngl {
    position: absolute;
    display: inline-block;
    left: 30%;
    top: -1rem;
    transform: translateX(-50%);
    border: 0.5rem solid transparent;
    border-bottom: 0.8rem solid rgba(112, 112, 112, 0.9);
    ;
}

.add-btn {
    display: inline-block;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
}

.edit-btn {
    position: relative;
    right: 2rem;
    top: 50%;
    transform: translateY(-50%);
}

.trash-btn {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.add-line {
    border-top: 2px solid var(--C00);
    border-bottom: 0px;
    position: relative;
}
</style>